import React, {useEffect, useState} from "react";
import PropTypes from 'prop-types'

import FarmActions from "./components/farmActions";
import useActiveWeb3React from "../../hooks/useWeb3";

// ----------------------------------------------------------------------

// ----------------------------------------------------------------------

FarmCard.propTypes = {
    farm: PropTypes.object.isRequired,
    displayApr: PropTypes.any,
    udePrice: PropTypes.any
};

export default function FarmCard({ farm, displayApr, udePrice, ...other }) {
    const { account } = useActiveWeb3React()
    const { id, stakeAssetImage, userData } = farm;

    const totalValueFormatted =
        farm.liquidity && farm.liquidity.gt(0)
            ? `$${farm.liquidity.toNumber().toLocaleString(undefined, { maximumFractionDigits: 0 })}`
            : '$0.00'

    const lpLabel = farm.lpSymbol && farm.lpSymbol.toUpperCase().replace('METAPLE', '')
    const earnLabel = farm.dual ? farm.dual.earnLabel : 'MLX + Fees'

    const weight = farm.poolWeight.toNumber() || 0
    // console.log(farm?.quoteToken.address)

    return (
    <>
        <div className="col-span-1">
            <div className="p-5 border-gray-300 shadow-xl hover:shadow-2xl bg-white dark:bg-black bg-opacity-100 dark:bg-opacity-50 rounded-3xl transition-all">
                <div className="relative">
                    <div className="bg-gradient-to-r from-sky-700 to-blue-700 bg-opacity-25 rounded-3xl overflow-hidden z-10">
                        <div className="px-6 py-6 transition-all flex align-middle justify-between relative z-10">
                            <div className="w-32 h-32 bg-circle bg-white rounded-full opacity-10 absolute pin-t ml-8 p-2 z-20 top-0 right-0 -mr-14 -mt-14" />
                            <div className="w-32 h-32 bg-circle bg-white rounded-full opacity-5 absolute pin-t ml-8 p-2 z-20 bottom-0 left-0 -ml-14 -mb-14" />
                            <div>
                                <div className="text-white font-semibold mb-3 text-sm">
                                    {lpLabel}
                                </div>
                            </div>
                            <div>
                                <div className="text-white font-semibold mb-3 text-sm">Earn MLX </div>
                                {/*<div className="text-white font-semibold text-xs cursor-help text-right" readOnly>*/}
                                {/*    {}*/}
                                {/*</div>*/}
                            </div>
                        </div>
                    </div>
                </div>
                <div className="flex align-middle justify-center -mt-6 mb-15 relative z-10">
                    <div className="rounded-full overflow-hidden -mx-2">
                        <img src={`./tokens/${farm?.quoteToken.address}.png`} className="rounded-full border-0 border-white" width={45} alt="" />
                    </div>
                    {/*<div className="rounded-full overflow-hidden -mx-2">*/}
                    {/*    <img src="images/dai.png" className="rounded-full border-0 border-white" width={45} alt="" />*/}
                    {/*</div>*/}
                </div>
                {/* <div class="flex justify-center my-6">
                                      <div class="w-auto px-5 pt-3 pb-2 bg-white text-green-800 text-sm rounded-full font-semibold">Active For LDO Only</div>
                                  </div> */}
                <div className="flex justify-between mb-1">
                    <div className="text-black dark:text-white dark:text-opacity-70 mb-3 text-xs font-semibold">
                        Earn
                    </div>
                    <div className="text-black dark:text-white font-semibold text-xs">
                        {earnLabel}
                    </div>
                </div>
                <div className="flex justify-between mb-1">
                    <div className="text-black dark:text-white dark:text-opacity-70 mb-3 text-xs font-semibold">
                        Multiplier
                    </div>
                    <div className="text-black dark:text-white font-semibold text-xs">
                        {Math.round(weight*100)}x
                    </div>
                </div>
                <div className="flex justify-between mb-1">
                    <div className="text-black dark:text-white dark:text-opacity-70 mb-3 text-xs font-semibold">
                        Liquidity
                    </div>
                    <div className="text-black dark:text-white font-semibold text-xs">
                        {totalValueFormatted}
                    </div>
                </div>
                <div className="flex justify-between mb-1">
                    <div className="text-black dark:text-white dark:text-opacity-70 mb-3 text-xs font-semibold">
                        Total APR
                    </div>
                    <div className="text-black dark:text-white font-semibold text-xs">
                        {displayApr}%
                    </div>
                </div>
                {/*<div className="flex justify-between mb-1">*/}
                {/*    <div className="text-black dark:text-white dark:text-opacity-70 font-semibold mb-3 text-xs font-semibold">  – Pool APY</div>*/}
                {/*    <div className="text-black dark:text-white font-semibold text-xs">5.19%</div>*/}
                {/*</div>*/}
                {/*<div className="flex justify-between mb-1">*/}
                {/*    <div className="text-black dark:text-white dark:text-opacity-70 font-semibold mb-3 text-xs font-semibold">  – LDO Farming APY</div>*/}
                {/*    <div className="text-black dark:text-white font-semibold text-xs">17.16%</div>*/}
                {/*</div>*/}
                {/*<button className="px-2 py-4 mt-2 text-center text-sm text-white bg-indigo-900 rounded-lg w-full font-medium hover:bg-blue-800 transition-all">*/}
                {/*    <span>Connect Wallet</span>*/}
                {/*</button>*/}
                <FarmActions farm={farm} lpLabel={lpLabel} account={account} udePrice={udePrice} />
            </div>
        </div>
    </>
    );
}
